import React, { Component } from 'react';
import "./Hub.css";
import Table from 'react-bootstrap/Table';
import { ResponsiveLine } from '@nivo/line';
import { ResponsiveBar } from '@nivo/bar';

const baseapi = window.location.protocol + "//" + window.location.host + "/api";
//const baseapi = "http://localhost:1337"
const url = baseapi + "/today";
const url2 = baseapi + "/today2";
const controlUrl = baseapi + "/controls";
const room = baseapi + "/roomssensors";
const settings = baseapi + "/settings";

export default class Hub extends Component {
  _isMounted = false;

  constructor() {
    super();
    this.state = {
      pricenow: '0',
      currency: 'öre/kwh',
      hour: '0',
      name: '',
      average: '0',
      tempis: '0',
      measured: '0',
      should: '0',
      percent: '0',
      max: 0,
      min: 3000,
      today: '',
      tomorrow: '',
      hours: [],
      prices: [],
      controls: [],
      tomspot: [],
      chart: null,
      chartdata: null,
      chartnow: null,
      chartnowdata: null,
      charttemp: null,
      charttempdata: null,
      time: new Date(),
      ute: ''
    }
  }

  componentDidMount() {
    console.log(window.location, this.props.location, this.props.location.pathname);
    this._isMounted = true;
    //let d = new Date();
    let d= this.state.time;
    let hour = d.getHours();
    let day = d.getDate();
    let day1 = d.getDate() + 1;
    let month = d.getMonth() + 1;
    let today = day + "/" + month;
    let tomorrow = day1 + "/" + month;
    let minutes = d.getMinutes();
    console.log("52", hour, minutes);

    hour = hour === 0 ? 24 : hour;


    fetch(room)
    .then(results => {
      //console.log("results", results);
      return results.json();
    })
    .then(data => {
        this.getOutside(data);
        console.log("roomssensors", data[0]);
        let tis = data && data[0] && data[0].tempis ? data[0].tempis.toFixed(1) : "";
        let showtemp = tis ? tis : 'Kommer visa ärvärde';
        this.setState({tempis: showtemp});
        if (data[0]) {
          this.setState({measured: data[0].measured});
          this.setState({should: data[0].should});
          this.setState({name: data[0].roomname});
        }
        return fetch(url)
    })
    .then(results => {
      //console.log("results", results);
      return results.json();
    })
    .then(data => {
      if (this._isMounted) {
        //console.log(data);
        let key = 'Hour' + hour;
        let curr = data.Currency === 'EUR' ? 'c/kwh' : 'öre/kwh';

        let avg = this.myformatter(data.Average);
        let pricenow = this.myformatter(data[key]);

        let extract = this.extractPrices(data);

        let temp = this.handlePrices(extract);
        //console.log("data", hour, data, avg);

        let chartdata = this.makeChartJson(extract, 'idag', "hsl(15, 100%, 34%)");
        let chartnowdata = this.makeChartNowJson(data.Average, data[key]);
        let charttempdata = this.makeChartTempJson();

        this.setState({average: avg});
        this.setState({pricenow: pricenow});
        this.setState({hour: hour});
        this.setState({currency: curr});
        this.setState({today: today});
        this.setState({tomorrow: tomorrow});
        this.setState({prices: temp});
        this.setState({chartdata: chartdata});
        this.setState({chartnowdata: chartnowdata});
        this.setState({charttempdata: charttempdata});
        this.MyResponsiveLine([this.state.chartdata]);
        this.MyResponsiveBar([this.state.chartnowdata]);
        this.MyResponsiveTempBar([this.state.charttempdata]);
        return fetch(controlUrl)
      }
    })
    .then(results2 => {
      console.log("results2", results2);
      return results2.json();
    })
    .then(data2 => {
      console.log("data2", data2);
      this.setState({controls: data2});
      if (hour >= 16) {
        return fetch(url2)
        .then(results => {
          //console.log("results", results);
          return results.json();
        })
        .then(data => {
          let extract = this.extractPrices(data, "tomorrow");
          //console.log(extract);
          let temp = this.handlePrices(extract);
          let chartdata1 = this.state.chartdata;
          let chartdata2 = this.makeChartJson(extract, 'imorgon', 'hsl(194, 38%, 30%)');
          //console.log("Båda chartdata:", chartdata1, chartdata2, typeof(chartdata1));
          const chartdata3 = [];
          if (chartdata1) chartdata3.push(chartdata1);
          if (chartdata2) chartdata3.push(chartdata2);

          this.setState({tomspot: temp});
          //console.log(chartdata3);
          this.MyResponsiveLine(chartdata3);
          return fetch(settings)
        })
      } else {
          return fetch(settings)
      }
    })
    .then(results3 => {
        console.log(results3);
        return results3.json();
    })
    .then(data3 => {
        this.setState({percent: data3.percent})
    })
    .catch(err => {
      console.log(err);
      if (err.name === 'AbortError') return;
      throw err;
    });
    setInterval(this.updateSec, 1000)
  }

  getOutside = (list) => {
      list.map(one => {
          if (one.roomname === "Ute") {
              const res = one.tempis ? one.tempis.toFixed(1) : "";
              this.setState({ute: res});
              console.log(this.state.ute);
          }
      });
  }

  updateSec =() => {
    this.setState({
      time: new Date()
    })
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  makeChartJson = (data, when, color) => {
      //console.log(data);
      let filtered = data.filter(x => x !== null);
      //x: i < 9 ? `0${i+1}` : `${i+1}`,
      let res = {
          'id': when,
          color: color,
          'data': filtered.map((y, i) => ({
            x: i < 9 ? `0${i+1}` : `${i+1}`,
            y,
          })),
        };
    //console.log(res);
    return res;
  }

  makeChartNowJson = (avg, pricenow) => {
      console.log(this.state.min, this.state.max);
    return {
      "what": this.state.currency,
      "max": this.state.max,
      "maxColor": "hsl(15, 100%, 34%)",
      "min": this.state.min,
      "minColor": "hsl(38, 79%, 56%)",
      "nu": parseFloat(pricenow)/10,
      "nuColor": "hsl(54, 84%, 65%)",
      "medel": parseFloat(avg)/10,
      "medelColor": "hsl(96, 32%, 40%)",
    }
  }

  makeChartTempJson = () => {
    let col = this.state.tempis > this.state.should ?
      "hsl(15, 100%, 34%)" :
      //"hsl(194, 35%, 56%)";
      "hsl(194, 37%, 57%)";

    return {
      "what": "Uppmätt: " + this.state.measured,
      "önskad": this.state.should,
      "önskadColor": "hsl(96, 32%, 40%)",
      "verklig": parseFloat(this.state.tempis),
      "verkligColor": col,
    }
  }


  myformatter = (nr) => {
      let divided = (parseFloat(nr) / 10).toFixed(1);
      divided = divided.toString();
      divided = divided.replace(".", ",");
      if (nr === "") {
          divided = null;
      }
      return divided;
  }

  extractPrices = (data, tomorrow=null) => {
      let temp = [];
      let hours = ['När'];
      let max = this.state.max;
      let min = this.state.min;
      for (let i = 1; i < 25; i++) {
          let key = 'Hour' + i;
          let hour = i < 10 ? '0' + i : i + "";
          let price = 0;
          if (i === 3) {
              key = 'Hour' + i + 'A';
              price = data[key] === "" ? null : (parseFloat(data[key]) / 10);
              if (price) {
                min = price < min && tomorrow === null ? price : min;
                max = price > max && tomorrow === null ? price : max;
                price = price.toFixed(1);
                temp.push(price);
                hours.push(hour);
              }
              //console.log(key, price);
              key = 'Hour' + i + 'B';
              hour = "VT";
          }
          price = data[key] === "" ? null : (parseFloat(data[key]) / 10);
          if (price) {
            min = price < min && tomorrow === null ? price : min;
            max = price > max && tomorrow === null ? price : max;
            price = price.toFixed(1);
            temp.push(price);
            hours.push(hour);
          }
    }
    //console.log(hours);
    this.setState({hours: hours});
    this.setState({max: max});
    this.setState({min: min});
    return temp;
  }

  handlePrices = (data) => {
    let temp =[];
      for (let i = 0; i < data.length; i++) {
        let nr = data[i] === null ? data[i] : data[i].toString();
        nr = nr === null ? nr : nr.replace(".", ",");
        temp.push(nr);
      }
    return temp;
  }

  MyResponsiveBar = (input) => {
    console.log(input);
    let temp = (
        <ResponsiveBar
          data={input}
          keys={[ 'max', 'min', 'nu', 'medel']}
          margin={{ top: 50, right: 50, bottom: 50, left: 60 }}
          padding={0.1}
          innerPadding={15}
          indexBy="what"
          groupMode="grouped"
          colors={({ id, data }) => data[`${id}Color`]}
          borderRadius={5}
          borderColor={{ from: 'color', modifiers: [ [ 'darker', 2 ] ] }}
          axisTop={null}
          axisRight={null}
          axisBottom={{ tickSize: 5, tickPadding: 5, tickRotation: 0, legend: '', legendOffset: 36 }}
          axisLeft={{
            tickSize: 5,
            tickPadding: 5,
            tickRotation: 0,
            legend: 'Idag',
            legendPosition: 'start',
            legendOffset: -40
          }}
          labelSkipWidth={20}
          labelSkipHeight={20}
          labelTextColor={'black'}
          label={d => `${d.value}`}
          enableGridY={true}
          legends={[
            {
                dataFrom: 'keys',
                anchor: 'top-left',
                direction: 'row',
                justify: false,
                translateX: 10,
                translateY: -20,
                itemsSpacing: 5,
                itemWidth: 50,
                itemHeight: 10,
                itemDirection: 'left-to-right',
                itemOpacity: 0.9,
                symbolSize: 10,
                effects: [
                    {
                        on: 'hover',
                        style: {
                            itemOpacity: 1
                        }
                    }
                ]
            }
          ]}
          tooltip={({ id, value, color }) => (
            <strong style={{ color }}>
              {id}: {value } {this.state.currency}
            </strong>
          )}
          theme={{
            tooltip: {
              container: {
                background: '#474747',
              },
            },
          }}
          animate={true}
          motionStiffness={90}
          motionDamping={15}
        />
      )
      this.setState({chartnow: temp});
  }

  MyResponsiveTempBar = (input) => {
    const CustomBarComponent = ({ x, y, width, height, color, data }) => 
    (
      <React.Fragment>
      <circle cx={x + width / 2} cy={y + height / 2} r={Math.min(width, height) / 2} fill={color} />
      <text x={x + width / 2} y={(y + height / 2) - (height / 6)} alignmentBaseline={"central"} textAnchor={'middle'} stroke={'#f8f9fa'} dy={".1em"} strokeWidth={'1px'}>{this.state.name }
      <tspan x={x + width / 2} y={(y + height / 2) + (height / 6)}>{data.value} {'\u00b0C'}</tspan>
      </text>
      </React.Fragment>
    );

      let temp = (
        <ResponsiveBar
          data={input}
          keys={[ 'verklig', 'önskad']}
          margin={{ top: 50, right: 40, bottom: 50, left: 40 }}
          padding={0}
          innerPadding={10}
          indexBy="what"
          groupMode="grouped"
          barComponent={CustomBarComponent}
          //colors={{ scheme: 'nivo' }}
          colors={({ id, data }) => data[`${id}Color`]}
          borderRadius={5}
          borderColor={{ from: 'color', modifiers: [ [ 'darker', 2 ] ] }}
          axisTop={null}
          axisRight={null}
          axisLeft={null}
          axisBottom={{ tickSize: 0, legend: '' }}
          labelSkipWidth={20}
          labelSkipHeight={20}
          labelTextColor={'black'}
          label={d => this.state.name + `: ${d.value} '\u00b0C'`}
          enableGridY={true}
          legends={[
            {
                dataFrom: 'keys',
                anchor: 'top-left',
                direction: 'row',
                justify: false,
                translateX: 10,
                translateY: -20,
                itemsSpacing: 5,
                itemWidth: 50,
                itemHeight: 20,
                itemDirection: 'left-to-right',
                itemOpacity: 0.9,
                symbolSize: 10,
                effects: [
                    {
                        on: 'hover',
                        style: {
                            itemOpacity: 1
                        }
                    }
                ]
            }
          ]}
          animate={true}
          motionStiffness={90}
          motionDamping={15}
        />
      )
      this.setState({charttemp: temp});
  }

  MyResponsiveLine = (chdata) => {
      console.log("chdata", chdata);
      let temp = (
        <ResponsiveLine
          data={chdata}
          margin={{ top: 50, right: 5, bottom: 50, left: 35 }}
          xScale={{ type: 'point' }}
          yScale={{ type: 'linear', min: 0, max: 'auto' }}
          axisTop={null}
          axisRight={null}
          axisBottom={{
              tickSize: 3,
              tickPadding: 3,
              tickRotation: 0,
              legend: 'Spotpriser',
              legendOffset: 35,
              legendPosition: 'start'
          }}
          axisLeft={{
              orient: 'left',
              tickSize: 3,
              tickPadding: 3,
              tickRotation: 0,
              legend: this.state.currency,
              legendOffset: -30,
              legendPosition: 'end'
          }}
          //colors={{ scheme: 'set1' }}
          colors={{ datum: 'color' }}
          pointSize={5}
          pointColor={{ from: 'color', modifiers: [] }}
          pointBorderWidth={2}
          pointBorderColor={{ from: 'color' }}
          pointLabel="y"
          pointLabelYOffset={60}
          enableSlices="x"
          legends={[
            {
                anchor: 'top-left',
                direction: 'row',
                justify: false,
                translateX: 10,
                translateY: -40,
                itemsSpacing: 0,
                itemDirection: 'left-to-right',
                itemWidth: 80,
                itemHeight: 20,
                itemOpacity: 0.9,
                symbolSize: 10,
                symbolShape: 'circle',
                symbolBorderColor: 'rgba(0, 0, 0, .9)',
                effects: [
                    {
                        on: 'hover',
                        style: {
                            itemBackground: 'rgba(0, 0, 0, .08)',
                            itemOpacity: 1
                        }
                    }
                ]
            }
        ]}
      />
    )
    this.setState({chart: temp});
  }

  render() {
    let prices = this.state.prices;
    let controls = this.state.controls;
    let hours = this.state.hours;
    let percent = this.state.percent;
    let text = null;
    let tdata = null;
    let showoutside = this.state.ute ? <p>Utetemp: { this.state.ute } {'\u00b0C'}</p> : "";

    let d= this.state.time;
    const h = d.getHours();
    const m = d.getMinutes();
    const s = d.getSeconds();

    if (this.state.hour >= 16) {
        text = <td>{this.state.tomorrow}</td>;
        tdata = this.state.tomspot.map((item,i) => <td key = {i}>{item}</td>);
    }

    return (
          <div className="hub-page">
            <h1>Välkommen till BehovsBoBoxen 2019</h1>
            <p>{h}:{(m < 10 ? '0' + m : m)}:{(s < 10 ? '0' + s : s)}
            </p>

            <div className="half">
                <div className="chartnow">
                  {this.state.chartnow}
                </div>
            </div>
            <div className="half">
              <div className="charttemp">
                  {this.state.charttemp}
                </div>
            </div>

            <Table striped bordered hover size="sm" responsive className="mytable">
              <caption>Styrning: <span className="normal"></span> Börtemp <span className="boxwarm"></span> Maxtemp <span className="boxcold"></span> Mintemp <span className="boxaway"></span> Bortatemp </caption>
              <thead>
                <tr>
                  {hours.map((item,i) => <th key = {i}>{item}</th>)}
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>{this.state.today}</td>
                  {prices.map((item,i) => <td key = {i}>{item}</td>)}
                </tr>
                <tr className="control"><td>{percent + '%'}</td>{controls.map((item,i) => <td key = {i} className={'class' + item}></td>)}</tr>
                <tr>{text}{tdata}</tr>
              </tbody>
            </Table>

            {showoutside}

            <div className="chart">
              {this.state.chart}
            </div>
        </div>
    );
  }
}